html, body {
    height: 100vh;
    margin: 0;
    padding: 0;
    font-family: 'Varela Round', sans-serif;
}

#root {
    width: inherit;
    height: inherit;
    margin: inherit;
    padding: inherit;
}

.swiper-container {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    width: 100%;
    height: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.swiper-container-vertical > .swiper-pagination-progressbar.swiper-pagination-progressbar-opposite {
    height: 6px;
}

.page-config-section {
    position: absolute !important;
    z-index: 1000;
    top: 30px;
    right: 30px;
}

.content {
    width: 60vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    margin: auto;
    align-items: center;
    justify-content: center;
    text-align: center;
}

@media screen and (max-width: 600px) {
    .content {
        width: 90vw;
    }
}

p {
    font-size: 2rem;
}

a {
    transition: 100ms;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}